.article {
    display: flex;
    flex-direction: row;
    margin: 2vw;
    border: 3px solid black;
    flex-basis: 100%;
}

.article__id {
    border-right: 3px solid black;
    flex-basis: 40vw;
    text-align: center
}

.article__title {
    flex-basis: 40vw;
    text-align: center;
    border-right: 3px solid black;
}

.article__body {
    flex-basis: 80vw;
    text-align: center;
    border-right: 3px solid black;
}

.article__buttons {
    display: flex;
    flex-direction: row;
    width: 30vw;
    margin: 1vw;
}
.article-list {
    display: flex;
    flex-direction: column;
}

.create-btn {
    text-align: right;
    margin: 2vw;
}
.create-article {
    margin: 3vw;
    border: 3px solid black;
    width: 50%;
    display: flex;
    flex-direction: column;
}

.create-article__title {
    width: 50%;
    margin: 1vw;
}

.create-article__body {
    width: 100%;
    margin: 1vw;
}
.edit-article {
    margin: 3vw;
    border: 3px solid black;
    width: 50%;
    display: flex;
    flex-direction: column;
}

.edit-article__title {
    width: 50%;
    margin: 1vw;
}

.edit-article__body {
    width: 100%;
    margin: 1vw;
}
body {
    background-color: whitesmoke;
}
